.mainfacts{padding:100px 60px 50px;box-sizing:border-box;position:relative}.mainfacts-header{font-style:normal;font-weight:normal;font-size:35px;line-height:41px;color:var(--white);text-align:center;margin-bottom:40px}.mainfacts-slide-capt{font-style:normal;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:var(--white);margin-top:40px}.mainfacts>.swiper-button-prev,.mainfacts .swiper-button-next{color:var(--facts-slide-button)}.mainfacts>.swiper-button-prev{left:10px}.mainfacts>.swiper-button-next{right:10px}.mainfacts>.swiper-button-disable{opacity:0}.mainfacts-pagination{display:flex;justify-content:center;margin-top:66px}.mainfacts-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid var(--facts-slide-bullet);opacity:1;transition:0.3s background-color}.mainfacts-pagination>.swiper-pagination-bullet-active{background-color:var(--facts-slide-bullet)}@media only screen and (max-width: 414px){.mainfacts{padding:70px 60px}.mainfacts-header{font-size:27px;line-height:32px;margin-bottom:30px}.mainfacts-slide-capt{font-size:17px;line-height:20px;margin-top:21px}.mainfacts-pagination{margin-top:21px}.mainfacts-pagination-bullet{width:12px;height:12px;border-radius:50%;margin:0 16px;background-color:transparent;border:2px solid var(--facts-slide-bullet);opacity:1;transition:0.3s background-color}}

.politics{padding:50px 0}.politics-header{font-style:normal;font-weight:normal;font-size:35px;line-height:41px;color:var(--white);text-align:center}.politics-subheader{font-style:normal;font-weight:normal;font-size:23px;line-height:27px;color:var(--white);margin:42px 0 30px}.politics-list{display:flex;flex-wrap:wrap;justify-content:space-between}.politics-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}.politics-list-item-name{width:100%;text-align:center;font-style:normal;font-weight:normal;font-size:23px;line-height:27px;color:#222222;background:rgba(255,255,255,0.83)}.politics-list-item-tooltip{position:absolute;font-style:normal;font-weight:normal;font-size:19px;line-height:22px;color:var(--black);left:100%;width:400px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:var(--tooltip-bg);padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.politics-list-item-tooltip-switched{left:auto;left:initial;right:100%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@media only screen and (max-width: 414px){.politics{padding:70px 0}.politics-header{font-size:27px;line-height:32px}.politics-subheader{display:none}.politics-list{flex-direction:column}.politics-list-item{width:auto;margin:13px 17px 18px}.politics-list-item:last-child{margin-bottom:0}.politics-list-item-name{font-size:23px;line-height:27px}.politics-mobile-tooltip{position:absolute;font-style:normal;font-weight:normal;font-size:15px;line-height:22px;color:var(--black);width:400px;pointer-events:none;background-color:var(--tooltip-bg);z-index:50;transform:translateX(-100%)}.politics-mobile-tooltip-show{transform:translateX(0)}.politics-mobile-tooltip-navbar{display:flex;align-items:center;justify-content:flex-end}.politics-mobile-tooltip-navbar-close1{width:40px;height:0px;border:2px solid var(--black);transform:rotate(45deg) translate(14px, 12px);margin:0 0 10px 0}.politics-mobile-tooltip-navbar-close2{width:40px;height:0px;border:2px solid var(--black);opacity:0;margin:0 0 10px 0}.politics-mobile-tooltip-navbar-close3{width:40px;height:0px;border:2px solid var(--black);transform:rotate(-45deg) translate(7px, -6px);margin:0 0 10px 0}}

.footer{border-top:1px solid var(--white)}.footer-sponsor{display:flex;align-items:center}.footer-sponsor-item{margin-right:120px}.footer-sponsor-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between;margin-bottom:88px}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px}.footer-nav-link{font-style:normal;font-weight:normal;font-size:17px;line-height:20px;color:var(--white);margin-bottom:20px;text-decoration:none}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{font-style:normal;font-weight:normal;font-size:17px;line-height:20px;width:366px;color:var(--white)}.footer-text{font-style:normal;font-weight:300;font-size:14px;line-height:14px;color:var(--white);margin-bottom:14px}.text-yellow{color:var(--yellow)}@media only screen and (max-width: 414px){.footer-sponsor{margin-bottom:58px}.footer-sponsor-item{margin-right:80px}.footer-sponsor-item:last-child{margin-right:0}.footer-text-wrap{flex-direction:column;align-items:center}.footer-nav{margin-bottom:112px}.footer-nav-link{font-size:15px;line-height:18px;text-align:center;margin-bottom:13px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{font-size:15px;line-height:18px;text-align:center}.footer-text{font-size:10px;line-height:10px;text-align:center;margin-bottom:10px}}

